<!--//-->
<template>

    <div class="juzhong">
        <span class="ticket_title_all margin_b_10"></span>
        <el-form ref="form" :model="form" label-width="120px">
            <el-form-item class="ticket_title" label=" :">
                <span>{{form.name}}</span>
            </el-form-item>
            <el-form-item label=" :">
                <span>{{form.cost}}    {{form.gain}}</span>
            </el-form-item>
            <el-form-item label=" :">
                <span class="details_des">&ensp;{{form.lowPoint}}&ensp;</span>
            </el-form-item>
            <el-form-item label=" :">
                <span>{{form.startDate}}{{form.endDate}}</span>
            </el-form-item>
            <el-form-item class="code_shop" label=" :">
                <span> {{form.shopnumber}} &#X3000;</span>
                <el-button type="text" @click="dialogVisible_shopslist = true"></el-button>
            </el-form-item>
            <el-dialog
                    title=""
                    v-model="dialogVisible_shopslist"
                    size="tiny"
                    top="20%">
                <p v-for="item in shops">{{item.shopName}}</p>
                <span slot="footer" class="dialog-footer">
                      <el-button @click="dialogVisible_shopslist = false"> </el-button>
                      <el-button type="primary" @click="dialogVisible_shopslist = false"> </el-button>
                  </span>
            </el-dialog>
            
            <el-form-item label=" :">
                <span class="details_des">{{form.memo}}</span>
            </el-form-item>

            <el-form-item>
                <el-button class="bottom_button" v-if="offlineStatus == '0'" size="small" :disabled="disabled" type="primary" @click="dialogVisible_down = true;"> </el-button>
                <el-button class="bottom_button" @click="back" size="small"> </el-button>
            </el-form-item>
            <el-dialog
                    title=""
                    v-model="dialogVisible_down"
                    size="tiny"
                    top="20%">
                    <p>?</p>
                    <p>,!</p>
                <span slot="footer" class="dialog-footer">
                      <el-button @click="dialogVisible_down = false"> </el-button>
                      <el-button type="primary" @click="down_active"> </el-button>
                  </span>
            </el-dialog>
        </el-form>
    </div>
</template>

<script>
    import {getActiveDetail,downactive} from '../../../api/api'

    export default {
        data() {
            return {
                offlineStatus:'',
                disabled:false,
                dialogVisible_down:false,
                activityId: '',
                shops: [],
                dialogVisible_shopslist: false,
                form: {
                    shopnumber:''
                }
            }
        },
        mounted: function () {
            let data = this.qs.stringify({
                activityId: this.activityId
            });
            getActiveDetail(data).then(res => {
                if (res.errorCode == 30005) {
                    this.$router.push({path: '/login'});
                } else {
                    this.form = res.content;
                    this.shops = res.content.shopList;
                    this.form.shopnumber = res.content.shopList.length;
                }
            })
        },
        methods: {
            //
            down_active(){
                this.dialogVisible_down = false;
                this.disabled = true;
                let data = this.qs.stringify({
                    id: this.activityId
                });
                downactive(data).then(res=>{
                    if(res.status == "success"){
                        this.$message.success("!");
                        this.$router.push({path:'/integralList'})
                    }
                    else if(res.status == "errro"){
                        this.disabled = false;
                        this.$message(',!')
                    }
                })
            },
            //
            back() {
                window.history.back();
            }
        },
        created: function () {
            let id = this.$route.query.id;
            let offlineStatus = this.$route.query.offlineStatus;
            this.offlineStatus = offlineStatus;
            this.activityId = id;
        }
    }
</script>

<style>
    .margin_b_10 {
        margin-bottom: 20px;
    }

    .juzhong {
        width: 450px;
        margin: 20px auto;
    }

    .details_des {
        display: inline-block;
        width: 300px;
    }

    .dialog_goodslist .el-table__body-wrapper {
        height: 280px;
    }
</style>



