<!---->
<template>
    <div class="zj-main padding_10">
       <div class="tab_header padding_t_l_r_0">
            <span> : </span>
            <el-select v-model="typeselectvalue" placeholder="" size="small">
                <el-option
                        v-for="item in typeselectdata"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            &#X3000;
            <span> : </span>
            <el-select v-model="statusselectvalue" placeholder="" size="small">
                <el-option
                        v-for="item in statusselectdata"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            &#X3000;
            <span> : </span>
            <el-date-picker
                v-model="activetime"
                size="small"
                type="daterange"
                :picker-options="pickerOptions2"
                :editable="editable"
                placeholder="">
            </el-date-picker>
            <el-button class="query_button" @click="getList" type="primary" size="small"> </el-button>
        </div>
        <div class="tab_list">
            <div class="block">
                <el-table
                :data="tableData">
                    <el-table-column prop="name" label="" >
                    </el-table-column>
                    <el-table-column prop="time" label="">
                        <template scope="scope">
                            <span>{{scope.row.activeStartDate}}</span> - <span>{{scope.row.activeEndDate}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="type" label="">
                        <template scope="scope">
                            <span v-if="scope.row.investmentType == 'PAYMENT_RESULT'"></span>
                            <span v-if="scope.row.investmentType == 'SHOP_DETAIL'"></span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="number_all" label="/()">
                        <template scope="scope">
                            <p v-if="scope.row.investmentStatus == 0||scope.row.investmentStatus == 1">/</p>
                            <div v-if="scope.row.investmentStatus == 2||scope.row.investmentStatus == 3">
                                <p>{{scope.row.verifyNum}} / {{scope.row.receiveNum}}</p>
                                <el-button @click="click_detail(scope.row.id)"  type="text" size="small"></el-button>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template scope="scope">
                            <p v-if="scope.row.investmentStatus == 0"></p>
                            <p v-if="scope.row.investmentStatus == 1">,</p>
                            <p v-if="scope.row.investmentStatus == 2"></p>
                            <p v-if="scope.row.investmentStatus == 3"></p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="play" label="">
                        <template scope="scope">
                            <el-button v-if="scope.row.offline == '0'" @click="closeactive(scope.row.id)"  type="text" size="small"><span style="color:#333"> | </span></el-button>
                            <router-link :to="{path:'consumedetail',query:{id:scope.row.id}}">
                                <el-button  type="text" size="small"></el-button>
                            </router-link>
                            <!--<router-link v-if="scope.row.investmentType == 'PAYMENT_RESULT'" :to="{path:'consumedetail',query:{id:scope.row.id}}">
                                <el-button  type="text" size="small"></el-button>
                            </router-link>-->
                            <!--<router-link v-if="scope.row.investmentType == 'SHOP_DETAIL'" :to="{path:'scancodedetail',query:{id:scope.row.id}}">
                                <el-button  type="text" size="small"></el-button>
                            </router-link> -->
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination 
                    @current-change="handleCurrentChange" 
                    :page-size="10" 
                    layout="total, prev, pager, next,jumper" 
                    :total="pageCount" 
                    class="foot_nav">
                </el-pagination>
                <!---->
                <el-dialog
                    title=""
                    v-model="dialogVisible_queryshops"
                    size="small"
                    top="20%">
                    <div class="">
                        <div class="block">
                            <el-table
                            :data="dataile_tableData">
                                <el-table-column
                                label=""
                                    type="index"
                                    width="50">
                                </el-table-column>
                                <el-table-column prop="brandName" label="" >
                                </el-table-column>
                                <el-table-column prop="voucherNum" label="">
                                </el-table-column>
                                <el-table-column prop="type" label="/">
                                    <template scope="scope">
                                        <p>{{scope.row.verifyNum}} / {{scope.row.receiveNum}}</p>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </div>
                    </div>
                </el-dialog>
                <!---->
                <el-dialog
                    title=""
                    v-model="dialogVisible_closeactive"
                    size="tiny"
                    top="30%">
                    <p style="text-align: center;">?</p>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible_closeactive = false"> </el-button>
                        <el-button type="primary" @click="closetrue_btn"> </el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
    </div>
</template>
<script>
    import {formateDate} from '../../../api/CommonMethods'
    import {getInvestmentPager,activeoffline,getSonVoucher} from '../../../api/api';
    export default {
        data() {
            return {
                pageNumber:'1',
                activeId:'',
                pageSize:'10',
                pageCount:0,
                activetime:'',
                editable:false,
                typeselectvalue:'',
                statusselectvalue:'',
                dialogVisible_queryshops:false,
                dialogVisible_closeactive:false,
                tableData:[],
                dataile_tableData:[],
                activeStartDate:'',
                activeEndDate:'',
                typeselectdata:[
                    {value: '', label: ''},
                    {value: 'PAYMENT_RESULT', label: ''},
                    {value: 'SHOP_DETAIL', label: ''},
                ],
                statusselectdata:[
                    {value: '', label: ''},
                    {value: '0', label: ''},
                    {value: '1', label: ','},
                    {value: '2', label: ''},
                    {value: '3', label: ''},
                ],
                pickerOptions2: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
            }
        },
        mounted: function() {
            this.getList();
        },
        methods: {
            //
            getList(){
                if(this.activetime[0]){
                    this.activeStartDate = formateDate(this.activetime[0]);
                    this.activeEndDate = formateDate(this.activetime[1]);
                }else{
                    this.activeStartDate = '';
                    this.activeEndDate = '';
                }
                let data = this.qs.stringify({
                    'investment.investmentType':this.typeselectvalue,
                    'investment.investmentStatus':this.statusselectvalue,
                    'investment.activeStartDate':this.activeStartDate,
                    'investment.activeEndDate':this.activeEndDate,
                    'pageNumber':this.pageNumber,
                    'pageSize':this.pageSize,
                });
                getInvestmentPager(data)
                    .then(res=>{
                        this.tableData = res.content.resultList;
                        this.pageCount = res.content.pageCount;
                })
            },
            //
            handleCurrentChange(val){
                this.pageNumber = val;
                this.getList();
            },
            ///  
            click_detail(id){
                this.dialogVisible_queryshops = true;
                let data = this.qs.stringify({
                    voucherId:id
                });
                getSonVoucher(data)
                    .then(res=>{
                        this.dataile_tableData = res.content.resultList;
                })
            },
            //
            closeactive(id){
                this.dialogVisible_closeactive = true;
                this.activeId = id;
            },
            //  
            closetrue_btn(){
                let data = this.qs.stringify({
                    voucherId:this.activeId
                });
                activeoffline(data)
                    .then(res=>{
                        if(res.status == 'error'){
                            this.$message.error(res.message);
                        }else if(res.status == 'success'){
                            this.$message.success('');
                        }
                        this.getList();
                })
                this.dialogVisible_closeactive = false;
            }
        }
    }
</script>
<style scoped>
    .padding_10{
        padding: 16px;
        width: auto;
    }
</style>


