<template>
    <div class="zj-main">
        <div class="ticket_show">
            <div>
                <img src="../../../assets/ticket.png" alt="">
                <!--<span>{{money}}</span>
                <p>:{{startTime}}{{endTime}}</p>-->
            </div>
        </div>
        <div class="ticket_rule ticket_rule_zs bottom_margin_50">
            <span class="ticket_title_all"></span>
            <el-form ref="form" :model="form" label-width="140px">
                <el-form-item label=" :">
                    <span>{{form.name}}</span>
                </el-form-item>
                <el-form-item label=" :">
                    <span>{{form.investmentStartDate}} ~ {{form.investmentEndDate}}</span>
                </el-form-item>
                <el-form-item label=" :">
                    <span>{{form.activeStartDate}} ~ {{form.activeEndDate}}</span>
                </el-form-item>
                <el-form-item  label=" :">
                    <span  > {{form.minCost}} </span>
                </el-form-item>
                <el-form-item label=" :">
                    <span> {{canyushopslength}} </span>
                    <el-button class="click_table" type="text" @click="dialogVisible_shopslist = true"></el-button>
                </el-form-item>
                <el-dialog
                    title=""
                    v-model="dialogVisible_shopslist"
                    size="tiny"
                    top="20%">
                    <div class="block">
                        <el-table
                        :data="tableData1"
                        style="width: 100%">
                            <el-table-column
                                label=""
                                type="index"
                                width="50">
                            </el-table-column>
                            <el-table-column
                                prop="shopName"
                                label="">
                            </el-table-column>
                            <el-table-column
                                prop="shopStatus"
                                label="">
                            </el-table-column>
                        </el-table>
                     </div>   
                    <!--<span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible_shopslist = false"> </el-button>
                        <el-button type="primary" @click="dialogVisible_shopslist = false"> </el-button>
                    </span>-->
                </el-dialog>
            </el-form>
            <span class="ticket_title_all "></span>
            <el-form ref="form" :model="form" label-width="140px">
                <el-form-item label=" :">
                    <span v-if="form.pickLimitNum == '9999'"></span>
                    <span v-if="form.pickLimitPeriod == 'D'"> {{form.pickLimitNum}} /</span>
                    <span v-if="form.pickLimitPeriod == 'W'"> {{form.pickLimitNum}} /</span>
                    <span v-if="form.pickLimitPeriod == 'M'"> {{form.pickLimitNum}} /</span>
                </el-form-item>
                <el-form-item class="ticket_limit" label=" :">
                    <span v-if="form.quantity == '9999'"></span>
                    <span v-if="form.quantity != '9999'">{{form.quantity}} </span>
                </el-form-item>
                <div style="display: inline-block;min-height: 600px;width: 600px">
                    <el-tabs v-model="editableTabsValue2" type="border-card">
                        <el-tab-pane
                            v-for="item in qlist"
                            :key="item.name"
                            :label="item.title"
                            :name="item.name"
                        >
                            <el-form class="margin_top_5"  label-width="140px">
                                <el-form-item label=" :">
                                    <span v-if="item.voucherType == 'RATE'"></span>
                                    <span v-if="item.voucherType == 'MONEY'"></span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span> {{item.showshopslistnumber}} </span>
                                    <el-button type="text" class="click_table" @click="dialogVisible_shopslist_quan = true"></el-button>
                                </el-form-item>
                                <el-form-item v-if="item.voucherType == 'MONEY'" label=" :">
                                    <span>{{item.voucherMoney}} </span>
                                </el-form-item>
                                <el-form-item v-if="item.voucherType == 'RATE'"  label=" :">
                                   <span>{{item.discount*10}} </span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span v-if="item.fullCost == '0.01'"></span>
                                    <span v-if="item.fullCost != '0.01'">{{item.fullCost}} </span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span>{{item.modifyDate}}</span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span>{{item.brandName}}</span>
                                </el-form-item>
                                <el-form-item label="Logo :">
                                    <div>
                                        <img style="width:100px;display: inline-block;height: auto;" :src="item.urlLogo" alt="">
                                    </div>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <p v-for="items in item.showinstruction">{{items}}</p>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span>1</span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span v-if="item.effectWay == 'IMMEDIATELY'"></span>
                                    <span v-if="item.effectWay == 'DELAY'"></span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span v-if="item.isShare == 'true'"></span>
                                    <span v-if="item.isShare == 'false'"></span>
                                </el-form-item>
                                <el-form-item v-if="item.voucherType == 'RATE'" label=" :">
                                    <span>{{item.maxAmount}} </span>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span v-if="item.show_useWeek == ''"></span>
                                    <p v-if="item.show_useWeek != ''" v-for="items in item.show_useWeek">
                                        {{items.useWeek}} {{items.useTime}} 
                                    </p>
                                </el-form-item>
                                <el-form-item label=" :">
                                    <span v-if="item.show_forbiddenUseDate == ''"></span>
                                    <span v-if="item.show_forbiddenUseDate != ''">{{item.show_forbiddenUseDate}} </span>
                                </el-form-item>
                                <el-dialog
                                    title=""
                                    v-model="dialogVisible_shopslist_quan"
                                    size="tiny"
                                    top="20%">
                                    <div class="block">
                                        <el-table
                                        :data="item.tableData2"
                                        style="width: 100%">
                                            <el-table-column
                                                label=""
                                                type="index"
                                                width="50">
                                            </el-table-column>
                                            <el-table-column
                                                prop="shopName"
                                                label="">
                                            </el-table-column>
                                            <!--<el-table-column
                                                label="">
                                            </el-table-column>-->
                                        </el-table>
                                    </div>  
                                    <!--<span slot="footer" class="dialog-footer">
                                        <el-button @click="dialogVisible_shopslist_quan = false"> </el-button>
                                        <el-button type="primary" @click="dialogVisible_shopslist_quan = false"> </el-button>
                                    </span>-->
                                </el-dialog>
                            </el-form>
                        </el-tab-pane>
                    </el-tabs>
                    <el-form-item class="margin_top_30">
                        <el-button type="primary" class="bottom_button" @click="back" size="small"> </el-button>
                    </el-form-item>
                </div>
            </el-form>
        </div>
    </div>
</template>

<script>
    import { getVoucher } from '../../../api/api';
    export default {
        data() {
            return {
                id:'',
                dialogVisible_shopslist:false,
                dialogVisible_shopslist_quan:false,
                editableTabsValue2:'',
                form:{},
                canyushopslength:0,
                qlist: [],
                tableData1:[],
            }
        },
        mounted:function () {
            this.getData();
        },
        methods: {
            //
            getData(){
                let data = this.qs.stringify({
                    voucherId:this.id
                });
                getVoucher(data)
                    .then(res=>{
                        this.form = res.content.investment;
                        this.qlist = res.content.sonList;
                        this.canyushopslength = res.content.shopList.length;
                        this.tableData1 = res.content.shopList;
                        this.showdata();
                })
            },
            
            showdata(){
                
                this.form.investmentStartDate = this.form.investmentStartDate.substring(0,this.form.investmentStartDate.length-2);
                this.form.activeEndDate = this.form.activeEndDate.substring(0,this.form.activeEndDate.length-2);
                this.form.activeStartDate = this.form.activeStartDate.substring(0,this.form.activeStartDate.length-2);
                this.form.investmentEndDate = this.form.investmentEndDate.substring(0,this.form.investmentEndDate.length-2);
                for(let i = 0;i<this.qlist.length;i++){
                    this.qlist[i].showshopslistnumber = this.qlist[i].suitShopList.length;
                    this.qlist[i].tableData2 = this.qlist[i].suitShopList;
                    if(this.qlist[i].voucherType == 'MONEY'){
                        this.qlist[i].title = this.qlist[i].voucherMoney + '';
                        this.qlist[i].name = i.toString();
                    }else if(this.qlist[i].voucherType == "RATE"){
                        this.qlist[i].title = this.qlist[i].discount*10 + '';
                        this.qlist[i].name = i.toString();
                    }
                    this.qlist[i].instruction = this.qlist[i].instruction.substring(2,this.qlist[i].instruction.length - 2);
                    this.qlist[i].showinstruction = [];
                    this.qlist[i].showinstruction = this.qlist[i].instruction.split('","');
                    if(this.qlist[i].useWeek == ''){
                        this.qlist[i].show_useWeek = '';
                    }else if(this.qlist[i].useWeek != ''){
                        this.qlist[i].show_useWeek=JSON.parse(this.qlist[i].useWeek);
                        for(let m =0;m<this.qlist[i].show_useWeek.length;m++){
                            this.qlist[i].show_useWeek[m].useTime = this.qlist[i].show_useWeek[m].useTime.replace(/,/g,'-').replace(/\^/g,' ; ');
                            this.qlist[i].show_useWeek[m].useWeek = this.qlist[i].show_useWeek[m].useWeek.replace(/1/,'').replace(/2/,'').replace(/3/,'').replace(/4/,'').replace(/5/,'').replace(/6/,'').replace(/7/,'');
                        }
                        this.qlist[i].show_useWeek
                    }
                    if(this.qlist[i].forbiddenUseDate == ''){
                        this.qlist[i].show_forbiddenUseDate = '';
                    }else if(this.qlist[i].forbiddenUseDate != ''){
                        this.qlist[i].show_forbiddenUseDate = this.qlist[i].forbiddenUseDate.replace(/,/g,'  ').replace(/\^/g,' ; ');
                    }
                }
                this.editableTabsValue2 = '0';
            },
            //
            back(){
                this.$router.go(-1);
            },

        },
        created:function(){
            let getid = this.$route.query.id;
            this.id = getid;
        }

    }
</script>

<style scoped>
    .ticket_show{
        width: 30%;
    }
    .ticket_rule {
        width: 69%;
    }
    .margin_top_30{
        margin-top: 30px;
    }
    .titlenamestyle{
        font-size: 14px;
        font-weight: 600;
    }
    .margin_top_5{
        margin-top: 5px;
    }
</style>


