<template>
    <div>
        <div v-if="message">
            <div class="ticket_show">
                <div>
                    <img src="../../../assets/images/ticket.png" alt="">
                    <span v-if="message.amount">{{message.amount}}</span>
                    <span v-if="message.amount">{{message.amount}}</span>
                    <p>:{{message.pick_start_date}}{{message.pick_end_date}}</p>
                </div>
            </div>
            <div class="ticket_rule">
                <span class="ticket_title_all"></span>
                <el-form ref="form" :model="form" label-width="140px">
                    <el-form-item class="ticket_title" label=" :">
                        <span>{{message.active_title}}</span>
                    </el-form-item>
                    <el-form-item class="ticket_downtype" label=" :">
                        <div v-if="message.voucher_type === ''">
                            <el-radio v-model="form.radio" label=""></el-radio>
                            <br>
                            <span> {{message.old_price}} , {{message.min_cost}} </span>
                            <span class="ticket_tip">:1022</span>
                        </div>
                        <div v-if="message.voucher_type === ''">
                            <!--<el-radio v-model="form.radio" label=""></el-radio>
                            <br>-->
                            <span> {{message.amount}} </span>
                        </div>
                    </el-form-item>
                    <el-form-item class="code_shop" label=" :">
                        <span> {{shopsLength}} &#X3000;</span>
                        <el-button type="text" @click="dialogVisible_shopslist = true"></el-button>
                        <el-dialog
                                title=""
                                v-model="dialogVisible_shopslist"
                                size="tiny"
                                top="20%">

                            <!--<div class="details_shoplist" v-for="item in shops">-->
                            <!--<p>{{item.shopName}}</p>-->
                            <!--<p v-if="item.receviedStatus == 2"></p>-->
                            <!--<p v-if="item.receviedStatus == 1"></p>-->
                            <!--<p v-if="item.receviedStatus == 0"></p>-->
                            <!--</div>-->
                            <table class="zj-table" border="0">
                                <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="item in shops">
                                    <td>{{item.shopName}}</td>
                                    <td v-if="item.receviedStatus == 2"></td>
                                    <td v-if="item.receviedStatus == 1"></td>
                                    <td v-if="item.receviedStatus == 0"></td>
                                </tr>
                                </tbody>
                            </table>
                            <span slot="footer" class="dialog-footer">
                            </span>
                        </el-dialog>
                    </el-form-item>
                    <el-form-item class="ticket_number" label=" :" v-if="message.quantity != 9999">
                        <span>{{message.quantity}}</span>
                    </el-form-item>
                    <el-form-item class="ticket_number" label=" :" v-if="message.quantity == 9999">
                        <span></span>
                    </el-form-item>
                    <el-form-item label=" :">
                        <span>{{message.pick_start_date}}{{message.pick_end_date}}</span>
                    </el-form-item>
                    <el-form-item label=" :" v-if="tableDataLength != 0">
                        <span> {{tableDataLength}} &#X3000;</span>
                        <el-button type="text" @click="clickmoregoods"></el-button>
                    </el-form-item>
                    <el-form-item class="ticket_number" label=" :">
                        <span> {{message.valid_to_day}} </span>
                    </el-form-item>
                    <!-- <el-form-item class="ticket_number" v-if="message.min_cost == 0" label=" :">
                        <span></span>
                    </el-form-item> -->
                    <!-- <el-form-item class="ticket_number" v-if="message.min_cost != 9999 && message.min_cost != 0" label=" :">
                        <span> {{message.min_cost}} </span>
                    </el-form-item> -->
                    <el-dialog
                            class="dialog_goodslist"
                            title=""
                            v-model="dialogVisible_goodslist"
                            size="small"
                            top="15%">
                        <span class=" line_height_30 font14"> :&nbsp;</span>
                        <el-select v-model="selectvalue" placeholder="" size="small" @change="clickmoregoods"
                                class=" margin_r_10">
                            <el-option
                                    v-for="item in shops"
                                    :key="item.shopId"
                                    :label="item.shopName"
                                    :value="item.shopId">
                            </el-option>
                        </el-select>

                        <el-table
                                :data="tableData"
                                class="margin_t_20"
                                style="width: 100%">
                            <el-table-column
                                    type="index"
                                    label=""
                                    width="120">
                            </el-table-column>
                            <el-table-column
                                    prop="goodsName"
                                    label="">
                            </el-table-column>
                            <el-table-column
                                    prop="goodsClassIfy"
                                    label="">
                            </el-table-column>
                            <el-table-column
                                    prop="goodsPrice"
                                    label="">
                            </el-table-column>
                        </el-table>
                        <span slot="footer" class="dialog-footer">
                        </span>
                    </el-dialog>
                </el-form>
                <span class="ticket_title_all"></span>
                <el-form ref="form" :model="form" label-width="140px">
                    <el-form-item class="ticket_title" label=" :" v-if="message.pick_limit_total != 9999">
                        <span>{{message.pick_limit_total}}</span>
                    </el-form-item>
                    <el-form-item class="ticket_title" label=" :" v-if="message.pick_limit_total == 9999">
                        <span></span>
                    </el-form-item>

                    <el-form-item class="ticket_downtype" label=" :" v-if="message.pick_limit_num != 9999">
                        <span>{{message.pick_limit_num}}</span>
                    </el-form-item>
                    <el-form-item class="ticket_downtype" label=" :" v-if="message.pick_limit_num == 9999">
                        <span></span>
                    </el-form-item>

                    <!--<el-form-item label=" :">
                        <span>{{message.needExchange}}</span>
                    </el-form-item>-->
                    <el-form-item v-if="message.needExchange == ''" label=" :">
                        <span>{{message.binding_point}} </span>
                    </el-form-item>
                    <!--<el-form-item v-if="form.code_radio == 2" class="setways" label=" :">-->
                    <!--<span >{{message.publish_channels}}</span>-->
                    <!--</el-form-item>-->
                    
                    <el-form-item  label=" :">
                        <p v-if="message.exchange_week == ''"></p>
                        <p v-if="message.exchange_week != ''"> {{message.exchange_week}} {{message.exchange_time}} </p>
                        
                    </el-form-item>
                    <el-form-item  label=" :">
                        <span v-if="message.use_week == ''"></span>
                        <p v-if="message.use_week != ''"> {{message.use_week}} {{message.use_time}} </p>
                    </el-form-item>
                    <el-form-item>

                        <el-button class="bottom_button" @click="back" size="small"> </el-button>
                        <el-button class="bottom_button" @click="dialogVisible_xiajia = true" size="small"
                                   type="primary" v-if="offline == '0'">
                             
                        </el-button>
                    </el-form-item>
                    <el-dialog
                            title=""
                            v-model="dialogVisible_xiajia"
                            size="tiny"
                            top="35%">
                        <p style="text-align: center;">,,?</p>
                        <span slot="footer" class="dialog-footer">
                      <el-button @click="dialogVisible_xiajia = false"> </el-button>
                      <el-button type="primary" @click="soldOut"> </el-button>
                  </span>
                    </el-dialog>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    import {getProductDetail, productSoldOut,getGoodsByShop} from '../../../api/api'

    export default {
        data() {
            return {
                selectvalue:'',
                offline:'',
                tableData: '',
                shops: '',
                dialogVisible_xiajia: false,
                dialogVisible_goodslist: false,
                dialogVisible_shopslist: false,
                form: {
                },
                message: '',
                shopsLength: '',
                tableDataLength: '',
                id: ''
            }
        },
        mounted: function () {
            let id = this.$route.query.id;
            let data = this.qs.stringify({
                voucherId: id
            });
            this.getDetail(data);

        },
        methods: {
            clickmoregoods(){
                 this.dialogVisible_goodslist = true;
                 let data = this.qs.stringify({
                     voucherId:this.id,
                     shopId:this.selectvalue,
                 })
                 getGoodsByShop(data)
                    .then(res =>{
                        if (res.errorCode == 30005) {
                            this.$router.push({path: '/login'});
                        }else if(res.errorCode == 10000){
                            this.tableData = res.content;
                        }
                    })
            },
            soldOut: function () {
                this.dialogVisible_xiajia = false;
                let data = this.qs.stringify({
                    voucherId: this.id,
                    active_id: this.message.active_id
                });
                productSoldOut(data)
                    .then(res => {
                        if (res.errorCode == 10000) {
                            this.$message.success("");
                            window.history.back();
                        } else if (res.errorCode == 30005) {
                            this.$router.push({path: '/login'});
                        }
                    })
            },
            getDetail: function (data) {
                getProductDetail(data)
                    .then(res => {


                        console.log(res)
                        if (res.errorCode == 30005) {
                            this.$router.push({path: '/login'});
                        }
                        this.message = res.content;
                        this.shops = res.content.shopList;
                        this.shopsLength = res.content.shopList.length;
                        this.tableData = res.content.goodsList;
                        this.tableDataLength = res.content.goodsList.length;
                        this.selectvalue = this.shops[0].shopId;
                    })
            },
            back() {
                window.history.back();
            },
        },
        created: function () {
            this.id = this.$route.query.id;
            this.offline = this.$route.query.offline;
//       var curRouter = this.$router.currentRoute;
//       this.$store.commit("dealData", {
//         path: curRouter.path,
//         name: curRouter.meta.name
//       });
        }

    }
</script>

