    .margin_r_10 {
        margin-right: 10px;
    }

    .details_shoplist {
        display: flex;
        /*outline: 1px solid orange;*/
        border-bottom: 1px solid #bfcbd9;
    }

    .details_shoplist > p {
        display: inline-block;
